import React from "react";
import { graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image";

const About = () => {
  //Getting images
  const data = useStaticQuery(graphql`
    query {
      profile: file(relativePath: { eq: "profile.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return (
    <div id="about">
      <div className="container">
        <Img
          fluid={data.profile.childImageSharp.fluid}
          className="profile-img"
        />
        <h3>Jesus Salcedo</h3>
        <span className="text-light text-sm">
          I'm a graphic designer and Web developer, I specialize in
          illustration, black and white pieces, also a tattoo artist apprentice.
          Mi prefer style is Blackwork and handpoke.
        </span>
      </div>
    </div>
  );
};
export default About;
