import React, { useEffect, useState } from "react";
import { graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image";
import { motion } from "framer-motion";

const Work = () => {
  const [filter, setFilter] = useState("all");

  useEffect(() => {}, [filter]);

  //Getting images
  const data = useStaticQuery(graphql`
    query {
      skullSnake: file(relativePath: { eq: "skullsnake.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      misakiMei: file(relativePath: { eq: "misakimei.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      demonDagger: file(relativePath: { eq: "demondagger.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      skullDeath: file(relativePath: { eq: "skulldeath.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      raven: file(relativePath: { eq: "raven.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      skullPider: file(relativePath: { eq: "skullpider.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      medusa: file(relativePath: { eq: "medusa.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      kafka: file(relativePath: { eq: "kafka.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      demone: file(relativePath: { eq: "demone.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      escarabajo: file(relativePath: { eq: "escarabajo.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      camus: file(relativePath: { eq: "camus.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      deidara: file(relativePath: { eq: "deidara.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      gaviota: file(relativePath: { eq: "gaviota.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      baphomet: file(relativePath: { eq: "baphomet.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      colorRose: file(relativePath: { eq: "rosacolor.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      sisters: file(relativePath: { eq: "sisters.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 800, quality: 100, jpegQuality: 100) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  //Constructing images
  const images = [
    {
      name: "Skull with Snake",
      src: data.skullSnake.childImageSharp.fluid,
      alt: "Skull Snake",
      tags: ["art", "blackwork", "sketch", "tattoo", "skull"],
    },
    {
      name: "Misaki Mei",
      src: data.misakiMei.childImageSharp.fluid,
      alt: "Misaki Mei",
      tags: ["art", "anime", "another"],
    },
    {
      name: "Demonic Dagger",
      src: data.demonDagger.childImageSharp.fluid,
      alt: "Demonic Dagger",
      tags: ["art", "blackwork", "demon"],
    },
    {
      name: "Skull from Death",
      src: data.skullDeath.childImageSharp.fluid,
      alt: "Skull from Death",
      tags: ["art", "blackwork", "skull"],
    },
    {
      name: "Skull spider",
      src: data.skullPider.childImageSharp.fluid,
      alt: "Skull spider",
      tags: ["art", "blackwork", "skull"],
    },
    {
      name: "Raven",
      src: data.raven.childImageSharp.fluid,
      alt: "Raven",
      tags: ["art", "blackwork", "raven"],
    },
    {
      name: "Medusa",
      src: data.medusa.childImageSharp.fluid,
      alt: "Medusa",
      tags: ["art", "blackwork", "medusa"],
    },
    {
      name: "Kafka",
      src: data.kafka.childImageSharp.fluid,
      alt: "MetaMorphous",
      tags: ["art", "blackwork", "kafka", "portrait"],
    },
    {
      name: "Escarabajo",
      src: data.escarabajo.childImageSharp.fluid,
      alt: "Escarabajo",
      tags: ["art", "blackwork", "tattoo", "escarabajo"],
    },
    {
      name: "Camus",
      src: data.camus.childImageSharp.fluid,
      alt: "Camus",
      tags: ["art", "blackwork", "skull", "portrait"],
    },
    {
      name: "Deidara",
      src: data.deidara.childImageSharp.fluid,
      alt: "Deidara",
      tags: ["art", "anime", "tattoo", "naruto"],
    },
    {
      name: "Baphomet",
      src: data.baphomet.childImageSharp.fluid,
      alt: "Camus",
      tags: ["art", "blackwork", "baphomet", "tattoo"],
    },
    {
      name: "Gaviota",
      src: data.gaviota.childImageSharp.fluid,
      alt: "Gaviota",
      tags: ["art", "blackwork", "tattoo", "traditional"],
    },
    {
      name: "Demone",
      src: data.demone.childImageSharp.fluid,
      alt: "Demone",
      tags: ["art", "blackwork", "demon", "tattoo"],
    },
    {
      name: "Rose",
      src: data.colorRose.childImageSharp.fluid,
      alt: "Rose",
      tags: ["art", "color", "rose", "neo"],
    },
    {
      name: "Sister",
      src: data.sisters.childImageSharp.fluid,
      alt: "Rose",
      tags: ["art", "blacwork", "arcane", "jinx", "vi"],
    },
  ];
  return (
    <div id="work" className="work">
      <div className="work-header">
        <div className="container">
          <h1>Work</h1>
          <div className="work__filter">
            <button onClick={() => setFilter("all")}>All</button>
            <button href="#" onClick={() => setFilter("anime")}>
              Anime
            </button>
            <button href="#" onClick={() => setFilter("blackwork")}>
              Blackwork
            </button>
            <button href="#" onClick={() => setFilter("color")}>
              Color
            </button>
          </div>

          <div className="card-container">
            {images.map((image, i) => {
              if (filter === "all" || image.tags.includes(filter)) {
                return (
                  <motion.div
                    className="card"
                    whileHover={{ scale: 1.1 }}
                    whileTap={{ scale: 0.9 }}
                    animate={{ x: [0, 100, 80, 50, 0] }}
                    transition={{ ease: "easeOut", duration: 0.2 }}
                    key={i}
                  >
                    <div className="card-img">
                      <Img fluid={image.src} alt={image.alt} />
                    </div>
                    <div className="card__info">
                      <h3>{image.name}</h3>
                      <p>{image.tags.join(", ")}</p>
                    </div>
                  </motion.div>
                );
              } else {
                return null;
              }
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Work;
