import React from "react";
import Header from "./Header";
import Footer from "./Footer";
import "../styles/App.scss";
const Layout = ({ children }) => {
  return (
    <div className="layout">
      <Header />
      <div className="separator"></div>
      <main>
        {children}
        <Footer />
      </main>
    </div>
  );
};
export default Layout;
